<template>
  <div class="root" @mousemove="onMouseMove">
    <div class="header" :class="{ 'hidden': !showHeader }">
      <Navigation />
    </div>

    <div class="content" style="margin-top: 70px;" :style="style">
      <div class="about">

        <b-container>
          <b-row>
            <b-col style="text-align:center">
              <h5 style="margin-bottom: 5px">バフ・デバフ組み合わせ検索</h5>
            </b-col>
          </b-row>
        </b-container>

        <div class="flex">
          <div class="text-nowrap">
            <b-container>
              <div style="text-align:center">
                <h6 style="margin: 5px 0px">バフ</h6>
              </div>
              <b-table small borderless hover :items="buffs" :fields="buffFields">
                <template #cell(label)="r">
                  <div class="flex">
                    <b-form-checkbox v-model="r.item.enabled" size="sm" plain></b-form-checkbox>
                    <div style="margin: auto 0; vertical-align: baseline;">
                      {{r.item.label}}
                    </div>
                  </div>
                </template>
                <template #cell(weight)="r">
                  <b-form-input style="width: 4em" v-model.number="r.item.weight" size="sm" type="number" class="input-param"></b-form-input>
                </template>
                <template #cell(limit)="r">
                  <div v-if="r.item.limit != undefined">
                    <b-form-input style="width: 4em" v-model.number="r.item.limit" size="sm" type="number" class="input-param"></b-form-input>
                  </div>
                </template>
              </b-table>
            </b-container>
          </div>

          <div class="text-nowrap">
            <b-container>
              <div style="text-align:center">
                <h6 style="margin: 5px 0px">デバフ</h6>
              </div>

              <b-table small borderless hover :items="debuffs" :fields="buffFields">
                <template #cell(label)="r">
                  <div class="flex">
                    <b-form-checkbox v-model="r.item.enabled" size="sm" plain></b-form-checkbox>
                    <div style="margin: auto 0; vertical-align: baseline;">
                      {{r.item.label}}
                    </div>
                  </div>
                </template>
                <template #cell(weight)="r">
                  <b-form-input style="width: 4em" v-model.number="r.item.weight" size="sm" type="number" class="input-param"></b-form-input>
                </template>
                <template #cell(limit)="r">
                  <div v-if="r.item.limit != undefined">
                    <b-form-input style="width: 4em" v-model.number="r.item.limit" size="sm" type="number" class="input-param"></b-form-input>
                  </div>
                </template>
              </b-table>
            </b-container>
          </div>

          <div class="text-nowrap">
            <b-container>
              <div style="text-align:center">
                <h6 style="margin: 5px 0px">除外リスト</h6>
              </div>

              <b-table small borderless hover :items="excluded" :fields="excludedFields">
                <template #cell(name)="r">
                  <img :src="getImageURL(r.item.name)" :title="r.item.name" with="50" height="50" />
                  <b-button @click="excluded.splice(excluded.indexOf(r.item), 1)">有効化</b-button>
                </template>
              </b-table>
            </b-container>
          </div>

          <div class="text-nowrap">
            <div>
              <b-container>
                <div style="text-align:center">
                  <h6 style="margin: 5px 0px">設定</h6>
                </div>

                <b-form-row v-for="(param, name, index) in options" :key="index">
                  <b-col style="text-align: right" align-self="end">
                    <label style="width: 15em" :for="`bs-param-${index}`">{{param.label}}</label>
                  </b-col>
                  <b-col>
                    <b-form-checkbox v-if="typeof(param.value) === 'boolean'" :id="`bs-param-${index}`" v-model="param.value" size="sm" plain></b-form-checkbox>
                    <b-form-input v-if="typeof(param.value) === 'number'" style="width: 3em" :id="`bs-param-${index}`" v-model.number="param.value" size="sm" type="number" class="input-param"></b-form-input>
                  </b-col>
                </b-form-row>
              </b-container>
            </div>

            <div>
              <div style="text-align:center">
                <h6 style="margin: 5px 0px">フィルタ</h6>
              </div>
              <div class="menu-widgets flex">
                <div class="widget">
                  <b-button-group size="sm" id="class_selector">
                    <b-button v-for="(c, i) in classFilter" :key="i" :pressed.sync="c.state" variant="outline-secondary">
                      <b-img-lazy :src="getImageURL(classes[i])" width="20%" />
                    </b-button>
                  </b-button-group>
                </div>
              </div>
              <div class="menu-widgets flex">
                <div class="widget">
                  <b-button-group size="sm" id="symbol_selector">
                    <b-button v-for="(c, i) in symbolFilter" :key="i" :pressed.sync="c.state" variant="outline-secondary">
                      <b-img-lazy :src="getImageURL('シンボル:'+symbols[i])" width="20%" />
                    </b-button>
                  </b-button-group>
                </div>
                <div class="widget">
                  <b-button-group size="sm" id="rareiry_selector">
                    <b-button v-for="(c, i) in rarityFilter" :key="i" :pressed.sync="c.state" variant="outline-secondary">
                      <b-img-lazy :src="getImageURL(rarities[i])" width="30%" />
                    </b-button>
                  </b-button-group>
                </div>
                <div class="widget">
                  <b-button-group size="sm" id="attack_type_selector">
                    <b-button v-for="(c, i) in damageTypeFilter" :key="i" :pressed.sync="c.state" variant="outline-secondary">
                      <b-img-lazy :src="getImageURL(damageTypes[i])" width="20%" />
                    </b-button>
                  </b-button-group>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div style="padding: 0px 10px 10px 10px">
          <b-container>
            <div style="margin-bottom: 10px">
              <b-button id="stat-highscore" @click="doTest()" style="margin-left: 5px">テスト</b-button>
            </div>
          </b-container>
        </div>
      </div>

      <template v-for="(r, ri) in result">
        <div class="character" :key="ri">
          <div class="flex">
            <div v-if="r.main" class="portrait">
              <b-img-lazy :src="getImageURL(r.main.character.name)" :alt="r.main.character.name" :id="'portrait_m_'+ri" width="100" height="100" rounded />
              <b-popover v-if="displayType>=1" :target="'portrait_m_'+ri" triggers="hover click blur" :delay="{show:0, hide:250}" no-fade :title="r.main.character.name" placement="top">
                <div class="flex">
                  <b-button @click="excluded.push(r.main.character)">このキャラを除外</b-button>
                </div>
              </b-popover>
            </div>
            <div v-if="r.main" class="detail" v-show="displayType >= 1">
              <div class="skills">
                <div class="skill" v-for="(skill, si) in r.main.skills" :key="si">
                  <div class="flex">
                    <div class="icon">
                      <b-img-lazy :src="getImageURL(skill.name)" with="50" height="50" :id="'skill_m_'+ri+'_'+si" />
                      <b-popover v-if="displayType>=1" :target="'skill_m_'+ri+'_'+si" triggers="hover focus" :delay="{show:0, hide:250}" no-fade :title="skill.name" placement="top">
                        <div class="flex">
                          <b-button @click="excluded.push(skill)">このスキルを除外</b-button>
                        </div>
                      </b-popover>
                    </div>
                    <div class="desc" v-show="displayType >= 2">
                      <div class="flex">
                        <h6>{{ skill.name }}</h6>
                        <div class="param-group" v-html="skillParamsToHtml(skill)"></div>
                      </div>
                      <p><span v-html="descToHtml(skill)"></span><span v-if="skill.note" class="note" v-html="noteToHtml(skill)"></span></p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="skills">
                <div class="skill" v-for="(skill, si) in r.main.equipments" :key="si">
                  <div class="flex">
                    <div class="icon">
                      <b-img-lazy :src="getImageURL(skill.name)" with="50" height="50" :id="'item_'+ri+'_'+si" />
                      <b-popover v-if="displayType>=1" :target="'item_'+ri+'_'+si" triggers="hover focus" :delay="{show:0, hide:250}" no-fade :title="skill.name" placement="top">
                        <div class="flex">
                          <b-button @click="excluded.push(skill)">このアイテムを除外</b-button>
                        </div>
                      </b-popover>
                    </div>
                    <div class="desc" v-show="displayType >= 2">
                      <div class="flex">
                        <h6>{{ skill.name }}</h6>
                        <div class="param-group" v-html="skillParamsToHtml(skill)"></div>
                      </div>
                      <p><span v-html="descToHtml(skill)"></span><span v-if="skill.note" class="note" v-html="noteToHtml(skill)"></span></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div v-if="r.support" class="portrait">
              <b-img-lazy :src="getImageURL(r.support.character.name)" :alt="r.support.character.name" :id="'portrait_s_'+ri" width="100" height="100" rounded />
              <b-popover v-if="displayType>=1" :target="'portrait_s_'+ri" triggers="hover focus" :delay="{show:0, hide:250}" no-fade :title="r.support.character.name" placement="top">
                <div class="flex">
                  <b-button @click="excluded.push(r.support.character)">このキャラを除外</b-button>
                </div>
              </b-popover>
            </div>
            <div v-if="r.support" class="detail" v-show="displayType >= 1">
              <div class="skills">
                <div class="skill" v-for="(skill, si) in enumerate(r.support.skills)" :key="si">
                  <div class="flex">
                    <div class="icon">
                      <b-img-lazy :src="getImageURL(skill.name)" with="50" height="50" :id="'skill_s_'+ri+'_'+si" />
                    </div>
                    <div class="desc" v-show="displayType >= 2">
                      <div class="flex">
                        <h6>{{ skill.name }}</h6>
                        <div class="param-group" v-html="skillParamsToHtml(skill)"></div>
                      </div>
                      <p><span v-html="descToHtml(skill)"></span><span v-if="skill.note" class="note" v-html="noteToHtml(skill)"></span></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="flex">
            <div v-if="r.summon" class="portrait">
              <b-img-lazy :src="getImageURL(r.summon.character.name)" :alt="r.summon.character.name" :id="'portrait_m_'+ri" width="100" height="100" rounded />
            </div>
            <div v-if="r.summon" class="detail" v-show="displayType >= 1">
              <div class="skills">
                <div class="skill" v-for="(skill, si) in r.summon.skills" :key="si">
                  <div class="flex">
                    <div class="icon">
                      <b-img-lazy :src="getImageURL(skill.name)" with="50" height="50" :id="'skill_m_'+ri+'_'+si" />
                      <b-popover v-if="displayType>=1" :target="'skill_m_'+ri+'_'+si" triggers="hover focus" :delay="{show:0, hide:250}" no-fade :title="skill.name" placement="top">
                        <div class="flex">
                          <b-button @click="excluded.push(skill)">このスキルを除外</b-button>
                        </div>
                      </b-popover>
                    </div>
                    <div class="desc" v-show="displayType >= 2">
                      <div class="flex">
                        <h6>{{ skill.name }}</h6>
                        <div class="param-group" v-html="skillParamsToHtml(skill)"></div>
                      </div>
                      <p><span v-html="descToHtml(skill)"></span><span v-if="skill.note" class="note" v-html="noteToHtml(skill)"></span></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>

      </template>
    </div>

  </div>
</template>

<script>import Navigation from './Navigation.vue'
import jsonMainActive from '../assets/main_active.json'
import jsonMainPassive from '../assets/main_passive.json'
import jsonMainTalents from '../assets/main_talents.json'
import jsonMainChrs from '../assets/main_characters.json'
import jsonSupportActive from '../assets/support_active.json'
import jsonSupportPassive from '../assets/support_passive.json'
import jsonSupportChrs from '../assets/support_characters.json'
import jsonItems from '../assets/items.json'
import jsonConstants from '../assets/constants.json'
import common from "./common";

export default {
  name: 'Lookup',
  components: {
    Navigation,
  },
  mixins: [common],

  data() {
    return {
      classes: jsonConstants.classes,
      symbols: jsonConstants.symbols,
      damageTypes: jsonConstants.damageTypes,
      rarities: jsonConstants.rarities,

      classFilter: [],
      symbolFilter: [],
      rarityFilter: [],
      damageTypeFilter: [],

      options: [],
      buffs: [],
      debuffs: [],
      excluded: [],

      buffFields: [
        {
          key: "label",
          label: "種類",
        },
        {
          key: "limit",
          label: "上限",
        },
        {
          key: "weight",
          label: "優先度",
        },
      ],
      excludedFields: [
        {
          key: "name",
          label: "対象",
        },
      ],
    };
  },

  created() {
    this.mainActive = structuredClone(jsonMainActive);
    this.mainPassive = structuredClone(jsonMainPassive);
    this.mainTalents = structuredClone(jsonMainTalents);
    this.mainChrs = structuredClone(jsonMainChrs).filter(a => !a.hidden);

    this.supActive = structuredClone(jsonSupportActive);
    this.supPassive = structuredClone(jsonSupportPassive);
    this.supChrs = structuredClone(jsonSupportChrs).filter(a => !a.hidden);

    this.items = structuredClone(jsonItems).filter(a => !a.hidden || a.slot == "アミュレット");
    this.weapons = this.items.filter(a => a.slot == "武器");
    this.armors = this.items.filter(a => a.slot == "鎧");
    this.helmets = this.items.filter(a => a.slot == "兜");
    this.accessories = this.items.filter(a => a.slot == "アクセサリ");

    this.setupCharacters(this.mainChrs, this.mainActive, this.mainPassive, this.mainTalents);
    this.setupCharacters(this.supChrs, this.supActive, this.supPassive);

    this.searchTable = new Map();
    for (let s of [...this.mainActive, ...this.mainPassive, ...this.mainTalents, ...this.supActive, ...this.supPassive, ...this.items])
      this.searchTable.set(s.name, s);

    for (let i = 0; i < this.mainChrs.length; ++i) {
      let chr = this.mainChrs[i];
      chr.index = i + 1;
      chr.objectType = "メイン";
      chr.classId = this.classes.findIndex(v => v == chr.class);
      chr.symbolId = this.symbols.findIndex(v => v == chr.symbol);
      chr.rarityId = this.rarities.findIndex(v => v == chr.rarity);
      chr.damageTypeId = this.damageTypes.findIndex(v => v == chr.damageType);
    }
    for (let i = 0; i < this.supChrs.length; ++i) {
      let chr = this.supChrs[i];
      chr.index = i + 1;
      chr.objectType = "サポート";
      chr.classId = this.classes.findIndex(v => v == chr.class);
      chr.rarityId = this.rarities.findIndex(v => v == chr.rarity);
      chr.damageTypeId = this.damageTypes.findIndex(v => v == chr.damageType);
    }
    for (let i = 0; i < this.items.length; ++i) {
      let item = this.items[i];
      item.index = i + 1;
      item.objectType = "アイテム";
      item.status = this.getItemStatus(item);
    }

    const setSlotActive = function (skill, ownerType) {
      skill.ownerType = ownerType;
      if (skill.buff) {
        for (let v of skill.buff) {
          v.valueType = `バフ:${v.type}`
          if (!v.slot)
            v.slot = `バフ:${ownerType}${v.onBattle ? ':戦闘時' : ''}:${v.type}`;
        }
      }
      if (skill.debuff) {
        for (let v of skill.debuff) {
          v.valueType = `デバフ:${v.type}`
          if (!v.slot)
            v.slot = `デバフ${ownerType}${v.onBattle ? ':戦闘時' : ''}:${v.type}`;
        }
      }
    };
    const setSlotPassive = function (skill, ownerType) {
      skill.ownerType = ownerType;
      if (skill.buff)
        for (let v of skill.buff)
          v.valueType = `バフ:${v.type}`
      if (skill.debuff)
        for (let v of skill.debuff)
          v.valueType = `デバフ:${v.type}`
    };
    const apply = function (array, func) {
      for (let v of array)
        func(v);
    };
    apply(this.mainActive, a => setSlotActive(a, "メイン"));
    apply(this.mainPassive, a => setSlotPassive(a, "メイン"));
    apply(this.mainTalents, a => setSlotPassive(a, "メイン"));
    apply(this.supActive, a => setSlotActive(a, "サポート"));
    apply(this.supPassive, a => setSlotPassive(a, "サポート"));
    apply(this.items, a => setSlotPassive(a, "アイテム"));

    this.mainChrs.sort((a, b) => b.date.localeCompare(a.date));
    this.supChrs.sort((a, b) => b.date.localeCompare(a.date));
    this.items.sort((a, b) => b.date.localeCompare(a.date));


    this.classFilter = [];
    this.symbolFilter = [];
    this.rarityFilter = [];
    this.damageTypeFilter = [];
    this.fillFilter(this.classFilter, this.classes);
    this.fillFilter(this.symbolFilter, this.symbols);
    this.fillFilter(this.rarityFilter, this.rarities);
    this.fillFilter(this.damageTypeFilter, this.damageTypes);

    const makeOptions = function (params) {
      let r = {};
      const add = function (name, label, value) {
        r[name] = {
          label: label,
          value: value,
        };
      }
      for (const v of params)
        add(v[0], v[1], v[2]);
      return r;
    };
    this.options = makeOptions([
      ["maxPickup", "上位n人を選出", 10],
      ["allowOnBattle", "戦闘時発動効果を含める", true],
      ["allowProbability", "確率発動効果を含める", true],
      ["allowSingleUnitBuff", "単体バフを含める", false],
      ["allowSymbolSkill", "シンボルスキルを含める", false],
      ["allowSupportActive", "サポートのアクティブを含める", true],
    ]);

    const makeParams = function (buffOrDebuff, types) {
      const make = function (t) {
        return {
          label: t,
          enabled: false,
          limit: 0,
          weight: 1,
          valueType: `${buffOrDebuff}:${t}`,
          isDebuff: buffOrDebuff == "デバフ"
        };
      }
      return types.map(a => make(a));
    };
    this.buffs = makeParams("バフ", [
      "アタック", "ディフェンス", "マジック", "レジスト", "クリティカル率", "クリティカルダメージ倍率",
      "与ダメージ", "与ダメージ(物理)", "与ダメージ(魔法)", "与ダメージ(スキル)", "与ダメージ(範囲スキル)", "与ダメージ(通常攻撃)",
      "ダメージ耐性", "ダメージ耐性(物理)", "ダメージ耐性(魔法)",
    ]);
    this.debuffs = makeParams("デバフ", [
      "アタック", "ディフェンス", "マジック", "レジスト",
      "与ダメージ", "与ダメージ(物理)", "与ダメージ(魔法)",
      "ダメージ耐性", "ダメージ耐性(物理)", "ダメージ耐性(魔法)",
    ]);
    this.excluded = [];
  },

  mounted() {
  },

  methods: {
    findItem(name) {
      const r = this.searchTable.get(name);
      if (!r)
        console.log(`${name} not found`);
      return r;
    },

    getParamClass(param) {
      return param.disabled() ? "disabled" : "";
    },

    filterMatchMainChr(chr) {
      return this.filterMatch(this.classFilter, chr.classId) &&
        this.filterMatch(this.symbolFilter, chr.symbolId) &&
        this.filterMatch(this.rarityFilter, chr.rarityId) &&
        this.filterMatch(this.damageTypeFilter, chr.damageTypeId);
    },
    filterMatchSupChr(chr) {
      return this.filterMatch(this.classFilter, chr.classId) &&
        this.filterMatch(this.rarityFilter, chr.rarityId) &&
        this.filterMatch(this.damageTypeFilter, chr.damageTypeId);
    },

    doTest() {
      let r = this.doSearch(this.options.maxPickup.value);
      console.log(r);
    },

    matchClass(item, chr) {
      return item && (!item.classes || item.classes.includes(chr.class));
    },
    compare(a, b) {
      return a == b ? 0 : a < b ? 1 : -1;
    },

    getValues(opt) {
      let r = {};
      for (const k in opt)
        r[k] = opt[k].value;
      return r;
    },

    *enumerate(...arrays) {
      for (let array of arrays) {
        if (array)
          yield* array;
      }
    },

    *enumerateEffects(skill) {
      yield* this.enumerate(skill.buff, skill.debuff);
    },

    doSearch(num) {
      let excluded = [...this.excluded];
      let targets = [...this.buffs.filter(a => a.enabled), ...this.debuffs.filter(a => a.enabled)];
      const opt = this.getValues(this.options);

      let totalAmountGlobal = {};
      let usedActiveSlotsGlobal = {};

      for (let v of targets)
        totalAmountGlobal[v.valueType] = 0;

      const buffCondition = function (skill, buff) {
        if (buff.isDebuff) {
          return (opt.allowOnBattle || !buff.onBattle) &&
            (opt.allowProbability || !buff.probability);
        }
        else {
          return (buff.target != "自身") &&
            (opt.allowSingleUnitBuff || buff.target != "単体") &&
            (opt.allowOnBattle || !buff.onBattle) &&
            (opt.allowProbability || !buff.probability);
        }
      };

      const getScore = function (chr) {
        if (excluded.includes(chr))
          return null;

        let totalAmount = { ...totalAmountGlobal };
        let usedActiveSlots = { ...usedActiveSlotsGlobal };
        let overriddenActiveSlots = {};

        const limitAmount = function (param, amount) {
          if (param.limit > 0)
            amount = Math.min(amount, Math.max(param.limit - totalAmount[param.valueType], 0));
          return amount;
        }.bind(this);

        const countSkill = function (skill, updateState = false) {
          if (excluded.includes(skill))
            return 0;
          if (!opt.allowSymbolSkill && this.matchTags(skill.tags, /^シンボルスキル$/))
            return 0;
          if (!opt.allowSupportActive && skill.skillType == "アクティブ" && skill.ownerType == "サポート")
            return 0;

          let score = 0;
          for (const v of this.enumerateEffects(skill)) {
            let p = targets.find(a => a.valueType == v.valueType);
            if (p && buffCondition(skill, v)) {
              if (v.onBattle && !v.duration)
                continue;

              const amount = limitAmount(p, v.value);
              if (updateState) {
                totalAmount[v.valueType] += amount;
              }
              if (skill.skillType == "アクティブ") {
                let prev = usedActiveSlots[v.slot];
                if (prev /*&& prev[0] >= amount*/) {
                  continue;
                }
                else if (updateState) {
                  usedActiveSlots[v.slot] = [amount, chr];
                  if (prev)
                    overriddenActiveSlots[v.slot] = prev;
                }
              }
              score += amount * p.weight;
            }
          }
          if (skill.summon) {
            let sch = skill.summon[0];
            for (const s of [sch.talent, ...sch.skills])
              score += countSkill(s);
          }
          return score;
        }.bind(this);

        const pickEquip = function (equipments) {
          equipments = equipments.filter(a => !excluded.includes(a) && this.matchClass(a, chr));
          equipments = equipments.map(a => [countSkill(a), a]);
          equipments.sort((a, b) => this.compare(a[0], b[0]));
          let r = null;
          if (equipments.length > 0 && equipments[0][0] > 0) {
            r = equipments[0];
          }
          return r;
        }.bind(this);

        const pickSkill = function (skills) {
          let passive = [];
          for (const skill of skills.filter(a => a.skillType != "アクティブ")) {
            if (excluded.includes(skill))
              continue;

            const score = countSkill(skill);
            if (score > 0)
              passive.push([score, skill]);
          }

          let active = [];
          for (const skill of skills.filter(a => a.skillType == "アクティブ")) {
            const score = countSkill(skill);
            if (score > 0)
              active.push([score, skill]);
          }
          active = active.sort((a, b) => this.compare(a[0], b[0]));
          for (let a of active)
            a[0] = countSkill(a[1]);
          active = active.filter(a => a[0] > 0);

          let r = [...passive, ...active].sort((a, b) => this.compare(a[0], b[0]))[0];
          if (!r || r[0] == 0)
            return null;
          if (r[1].summon) {
            let sch = r[1].summon[0];
            let sr = [sch.talent, ...sch.skills].map(a => [countSkill(a), a]).filter(a => a[0] > 0).map(a => a[1]);
            let summon = { character: sch, skills: sr };
            r.push(summon);
          }
          return r;
        }.bind(this);


        let skills = [];
        let equipments = [];

        if (chr.talent) {
          let tmp = [countSkill(chr.talent), chr.talent];
          if (tmp[0] > 0) {
            countSkill(tmp[1], true);
            skills.push(tmp);
          }
        }

        let summon = undefined;
        if (chr.skills) {
          let tmpSkills = [...chr.skills];
          for (let i = 0; i < 3; ++i) {
            let r = pickSkill(tmpSkills);
            if (!r)
              break;

            tmpSkills.splice(tmpSkills.indexOf(r[1]), 1);
            skills.push(r);
            countSkill(r[1], true);
            if (r.length == 3) {
              summon = r[2];
              for (let ss of summon.skills) {
                countSkill(ss, true);
              }
            }
          }
        }
        if (chr.objectType == "メイン") {
          for (let equips of [this.weapons, this.armors, this.helmets, this.accessories]) {
            let r = pickEquip(equips);
            if (r) {
              equipments.push(r);
              countSkill(r[1], true);
            }
          }
        }

        return {
          score: [...skills, ...equipments].reduce((t, a) => t + a[0], 0),
          character: chr,
          skills: skills.map(a => a[1]),
          equipments: equipments.map(a => a[1]),
          summon: summon,

          totalAmount: totalAmount,
          usedActiveSlots: usedActiveSlots,
          overriddenActiveSlots: overriddenActiveSlots,
        };
      }.bind(this);


      let result = [];
      let mainChrs = this.mainChrs.filter(a => this.filterMatchMainChr(a));
      let supChrs = this.supChrs.filter(a => this.filterMatchSupChr(a));
      for (let i = 0; i < num; ++i) {
        let r = { score: 0 };

        const pickBest = function (chrs) {
          let ordered = chrs.map(a => getScore(a)).filter(a => a && a.score > 0).sort((a, b) => this.compare(a.score, b.score));
          return ordered.length ? ordered[0] : null;
        }.bind(this);

        const updateState = function (field, t) {
          r[field] = t;
          r.score += t.score;
          if (t.summon) {
            r.summon = t.summon;
            delete t.summon;
          }
          totalAmountGlobal = t.totalAmount;
          usedActiveSlotsGlobal = t.usedActiveSlots;
        };

        let bestMain = pickBest(mainChrs);
        if (bestMain)
          updateState("main", bestMain);

        let bestSup = pickBest(supChrs);
        if (bestSup)
          updateState("support", bestSup);

        for (let v of [r.main, r.support]) {
          if (!v)
            continue;
          excluded.push(v.character);
          for (const s of this.enumerate(v.skills, v.equipments))
            excluded.push(s);
        }
        if (!bestMain && !bestSup)
          break;

        result.push(r);
      }
      return result;
    },

  },

  computed: {
    result() {
      return this.doSearch(this.options.maxPickup.value);
    }
  }

}</script>

